html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
}
body.ow {
  overflow: hidden;
}
@media (min-width: 480px) {
  body.ow {
    overflow: auto;
  }
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

p {
  margin: 0;
}

h2 {
  color: #9092c7;
  font-weight: 600;
}

.barra {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: 5rem 3rem;
  justify-content: space-between;
  align-items: center;
  margin: 1.5rem auto;
}
@media (min-width: 768px) {
  .barra {
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: 0;
    padding: 3rem 0;
  }
}
.barra .logo {
  color: #9092c7;
  font-family: "Over the Rainbow", cursive;
  font-weight: bold;
  font-size: 3rem;
  grid-column: 1/2;
}
.barra .menu {
  grid-row: 2/3;
  font-size: 2.5rem;
  cursor: pointer;
}
@media (min-width: 480px) {
  .barra .menu {
    display: none;
  }
}
.barra .login {
  grid-column: 2/3;
  font-size: 2rem;
  font-family: "Montserrat", sans-serif;
  color: #000000;
  text-align: right;
  transition: color 0.2s ease;
  position: relative;
}
.barra .login::before {
  content: " ";
  width: 1rem;
  height: 1px;
  background-color: darkgoldenrod;
  position: absolute;
  bottom: 0;
  transition: width 0.1s ease;
}
.barra .login:hover::before {
  width: 15%;
}
.barra .login:hover {
  color: #9092c7;
}
@media (min-width: 768px) {
  .barra .login {
    grid-column: 3/4;
  }
}

ul {
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.2s ease, visibility 0.2s ease;
}
@media (min-width: 768px) {
  ul {
    opacity: 1;
    visibility: visible;
  }
}
ul.mostrar {
  opacity: 1;
  visibility: visible;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  position: absolute;
  top: 8rem;
  left: 0rem;
  padding: 3rem;
  background-color: rgb(202, 154, 202);
}
ul.mostrar li {
  margin: 2rem 0;
}
@media (min-width: 480px) {
  ul {
    display: flex;
    gap: 2.5rem;
  }
  ul.mostrar {
    background: none;
    position: unset;
    flex-direction: row;
  }
}
ul li {
  list-style: none;
}
ul li a {
  font-size: 2rem;
  font-family: "Montserrat", sans-serif;
  color: #000000;
  transition: color 0.2s ease;
}
ul li a:hover {
  color: #9092c7;
}

.bienvenida {
  width: 100%;
  height: 50%;
  background-image: url("/../img/banner_desk.webp");
  background-color: rgba(243, 84, 243, 0.3);
  background-blend-mode: multiply;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
.bienvenida .titulo {
  font-size: 9rem;
  font-family: "Over the Rainbow", cursive;
  text-align: center;
  font-weight: 400;
  margin: 0;
  padding: 5% 0;
  color: #ffffff;
}
@media (min-width: 480px) {
  .bienvenida .titulo {
    font-size: 10rem;
    padding: 10% 0;
  }
}
@media (min-width: 768px) {
  .bienvenida .titulo {
    font-size: 18rem;
  }
}
@media (min-width: 1200px) {
  .bienvenida .titulo {
    font-size: 23rem;
  }
}

.nosotros {
  margin: 3rem auto;
  text-align: center;
}
.nosotros h2 {
  font-size: 3rem;
}

.servicios {
  text-align: center;
}
.servicios .tipos a {
  display: block;
  text-align: center;
  margin: 1rem 0;
  border: thin 1px solid;
  padding: 2rem;
  font-size: 1.5rem;
  font-weight: bold;
}
.servicios .tipos a:hover {
  background-color: #abaef5;
  color: #000000;
}
.servicios .tipos a:nth-child(odd) {
  background-color: #abaef5;
}
.servicios .tipos a:nth-child(odd):hover {
  background-color: #ffffff;
  color: #abaef5;
}
@media (min-width: 768px) {
  .servicios .tipos {
    display: flex;
    justify-content: space-around;
    gap: 1rem;
  }
  .servicios .tipos a {
    flex: 1;
  }
}

@media (min-width: 480px) {
  .galeria {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
  }
  .galeria img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center center;
    transition: transform 0.2s ease;
  }
}
@media (min-width: 768px) {
  .galeria {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 1200px) {
  .galeria {
    grid-template-columns: repeat(7, 1fr);
    grid-template-rows: repeat(4, 23rem);
  }
  .galeria img:hover {
    transform: scale(1.03);
  }
  .galeria img:nth-child(1) {
    grid-column: 1/3;
  }
  .galeria img:nth-child(2) {
    grid-column: 3/6;
    grid-row: 1/3;
  }
  .galeria img:nth-child(3) {
    grid-column: 6/8;
    object-position: bottom;
  }
  .galeria img:nth-child(4) {
    grid-column: 1/3;
    grid-row: 2/4;
  }
  .galeria img:nth-child(5) {
    grid-column: 3/6;
    grid-row: 3/5;
  }
  .galeria img:nth-child(6) {
    grid-column: 6/8;
    grid-row: 2/4;
  }
  .galeria img:nth-child(7) {
    grid-column: 1/3;
  }
  .galeria img:nth-child(8) {
    grid-column: 6/8;
  }
}

.beneficios {
  background-color: #9092c7;
  padding: 4rem;
  margin: 2rem 0;
}
@media (min-width: 480px) {
  .beneficios {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    justify-content: space-around;
  }
}
@media (min-width: 768px) {
  .beneficios {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    justify-content: space-around;
  }
}
.beneficios div {
  text-align: center;
}
@media (min-width: 768px) {
  .beneficios div {
    border-right: thin solid #ffffff;
    padding: 2rem;
  }
}
.beneficios div:last-child {
  border: none;
}
.beneficios i {
  color: #ffffff;
  font-size: 4rem;
}

.testimonios .clientes {
  display: grid;
}
@media (min-width: 480px) {
  .testimonios .clientes {
    grid-template-columns: repeat(1, 1fr);
    justify-content: space-evenly;
    gap: 2rem;
  }
}
@media (min-width: 768px) {
  .testimonios .clientes {
    grid-template-columns: repeat(4, 1fr);
  }
}
.testimonios .cliente {
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  border-radius: 2rem;
  padding: 2rem;
  text-align: center;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
  margin: 0.5rem 0;
}
.testimonios .cliente img {
  width: 8rem;
  height: 8rem;
  border: thin solid #9092c7;
  border-radius: 50%;
  object-fit: cover;
  object-position: center;
}
.testimonios .cliente .detalle {
  font-size: 1.2rem;
  text-align: justify;
}
.testimonios .cliente .detalle .nombre {
  font-size: 1.4rem;
  font-weight: bold;
  margin-bottom: 1rem;
}
.testimonios .cliente .detalle .descripcion {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 4;
  overflow: hidden;
  line-height: 1;
  margin: 1rem 0;
}

.sidebar {
  margin: 1.5rem 0;
  display: grid;
  background-image: url("/img/banner_desk_contact.webp");
  padding: 4rem;
  place-items: center;
  text-align: center;
  background-color: rgb(252, 170, 234);
  background-blend-mode: multiply;
  background-repeat: no-repeat;
  background-size: cover;
  color: #ffffff;
}

footer {
  padding: 2rem;
  font-weight: 300;
}
footer h3 {
  font-weight: 400;
}
@media (min-width: 768px) {
  footer {
    display: grid;
    grid-template-columns: 1fr 25rem 1fr;
    gap: 2rem;
  }
}
footer .compania,
footer .cuenta,
footer .informacion {
  border-bottom: thin solid rebeccapurple;
}
@media (min-width: 768px) {
  footer .compania,
  footer .cuenta,
  footer .informacion {
    border-bottom: none;
  }
}
footer .compania {
  text-align: justify;
  font-size: 1.3 rem;
}
footer .cuenta {
  text-align: center;
}
footer .cuenta a {
  display: block;
  margin: 1rem 0;
}
footer .informacion {
  text-align: center;
}
footer ul {
  display: block;
  padding: 0;
}
footer ul li {
  margin: 1rem 0;
}

.copy {
  width: 100%;
  background-color: #9092c7;
  padding: 1rem;
  text-align: center;
  font-family: "Over the Rainbow", cursive;
}

.contenido-login {
  width: 80%;
  margin: auto auto;
}
.contenido-login h2 {
  font-size: 3rem;
  color: #000000;
}
@media (min-width: 768px) {
  .contenido-login {
    justify-content: center;
    padding: 2rem;
  }
}
.contenido-login .img {
  display: flex;
  width: 100%;
  background-image: url("/../img/banner_desk_contact.webp");
  background-repeat: no-repeat;
  background-position: top left;
  background-size: auto;
  justify-content: space-between;
  flex-direction: column;
  border-radius: 2rem;
}
.contenido-login .img.img-crear {
  background-image: url("/../img/banner_desk.webp");
  background-position: center;
}
@media (min-width: 768px) {
  .contenido-login .img {
    flex-direction: row;
    background-position: left;
    background-size: cover;
  }
}
.contenido-login .img .div {
  width: 100%;
  font-size: 1.2rem;
  display: flex;
  flex-direction: column;
  height: 40vh;
  margin: 0 auto;
  justify-content: center;
  padding: 0 2rem;
}
@media (min-width: 480px) {
  .contenido-login .img .div {
    width: 80%;
    font-size: 1.5rem;
  }
}

.formulario {
  width: 100%;
  background-color: rgba(144, 146, 199, 0.7176470588);
  padding: 2rem;
}
@media (min-width: 768px) {
  .formulario {
    width: 100%;
  }
}
.formulario .campo {
  margin: 2rem auto;
}
.formulario .campo input {
  margin: 0.5rem 0 0 0;
  width: 100%;
  border: none;
  border: thin solid #9092c7;
  padding: 1rem;
}
.formulario .opciones {
  display: flex;
  justify-content: space-between;
  font-size: 1.3rem;
}

.titulo {
  text-align: center;
}

.mensaje {
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #9092c7;
  border-radius: 3rem;
  text-align: center;
}
.mensaje .nombre-pagina {
  font-family: "Over the Rainbow", cursive;
}
.mensaje .boton-mensaje {
  background-color: burlywood;
}

.alerta {
  margin: 0.5rem 0;
  text-align: center;
  font-weight: 500;
  border-radius: 1rem;
  padding: 0.5rem 0.7rem;
  width: 100%;
}
.alerta.error {
  background-color: red;
  color: white;
}
.alerta.exito {
  background-color: rgb(0, 180, 252);
  color: rgb(0, 0, 0);
}

.contenedor-servicios {
  background-position: center;
  background-size: cover;
  background-repeat: repeat;
}
@media (min-width: 768px) {
  .contenedor-servicios {
    background-image: url("/../img/imgs.jpg");
    display: flex;
    height: 100vh;
    margin-bottom: 1.5rem;
  }
}
.contenedor-servicios .img {
  flex: 1;
  height: 20rem;
  background-image: url("/../img/uña7.jpg");
  background-position: top;
  background-size: cover;
  background-repeat: repeat;
}
@media (min-width: 768px) {
  .contenedor-servicios .img {
    height: auto;
    background-image: none;
  }
}
.contenedor-servicios .servicios {
  background-color: #9092c7;
  flex: 1;
  overflow-y: scroll;
}
.contenedor-servicios .servicios h2 {
  color: #ffffff;
  font-size: 3rem;
  padding: 1rem 0;
}
.contenedor-servicios .servicios::-webkit-scrollbar {
  width: 0;
  height: 0;
}
.contenedor-servicios .servicio {
  flex: 1;
  padding: 1rem;
  margin: 0.5rem 0;
  border-bottom: thin ridge black;
}
@media (min-width: 480px) {
  .contenedor-servicios .servicio {
    display: flex;
    justify-content: space-around;
    align-items: center;
  }
}
.contenedor-servicios .servicio .nombre {
  font-size: 1.5rem;
  font-weight: bold;
}
.contenedor-servicios .servicio .opciones {
  display: flex;
  justify-content: center;
  gap: 1.5rem;
  margin: 0.5rem 0;
}
@media (min-width: 768px) {
  .contenedor-servicios .servicio .opciones {
    flex-direction: column;
  }
}

.tabs {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}
@media (min-width: 768px) {
  .tabs {
    flex-direction: row;
  }
}
.tabs button {
  flex: 1;
  padding: 1rem;
  font-size: 2rem;
  cursor: pointer;
  transition: background 0.1s ease;
  font-weight: bold;
}
.tabs button:hover {
  color: #ffffff;
  background-color: #7576a1;
}
.tabs button.actual {
  background-color: #9092c7;
  color: #ffffff;
}

.paginacion {
  display: flex;
  justify-content: space-between;
}
.paginacion .boton {
  margin: 1rem;
  background-color: blueviolet;
}

.boton {
  background-color: #9092c7;
  padding: 1rem 2rem;
  display: block;
  font-weight: bold;
  color: #000000;
  font-size: 2rem;
  border-radius: 2rem;
  transition: background-color 0.2s ease;
  cursor: pointer;
  border: none;
  text-align: center;
  margin: 2rem auto;
}
.boton:hover {
  background-color: #6e71b6;
}

.btn_a {
  background-color: rgb(159, 237, 240);
  padding: 0.5rem 0.7rem;
  font-weight: bold;
  border-radius: 1rem;
  display: inline-block;
}

.btn_e {
  background-color: rgb(247, 144, 76);
  padding: 0.6rem 1.5rem;
  font-weight: bold;
  border-radius: 1rem;
  border: none;
  display: inline-block;
  cursor: pointer;
}

.swal2-html-container {
  font-size: 2rem !important;
}

.swal2-actions {
  font-size: 1.5rem;
}

.seccion {
  padding: 2rem 0;
  display: none;
}

.mostrar {
  display: block;
}

.ocultar {
  visibility: hidden;
}

.listado-servicios {
  padding: 1rem 0;
  margin: 1rem 0;
}
.listado-servicios .servicio_div {
  display: flex;
  justify-content: space-evenly;
  border-bottom: thin solid blue;
